import React from "react";
import { Timeline, Icon } from "antd";

import "./index.less";

export default class TimeLine extends React.Component {
    render() {
        return (
            <section className="timeline">
                <Timeline className="timeline-group" mode="alternate">
                    <Timeline.Item>GitHub isuess blog</Timeline.Item>
                    <Timeline.Item></Timeline.Item>
                    <Timeline.Item color="green">express</Timeline.Item>
                    <Timeline.Item color="green">todomvc</Timeline.Item>
                    <Timeline.Item
                        dot={
                            <Icon
                                type="clock-circle-o"
                                style={{ fontSize: "16px" }}
                            />
                        }
                    >
                        2018
                    </Timeline.Item>
                    <Timeline.Item color="red"></Timeline.Item>
                    <Timeline.Item color="red"></Timeline.Item>
                    <Timeline.Item color="red"></Timeline.Item>
                    <Timeline.Item color="red"></Timeline.Item>
                    <Timeline.Item color="red"></Timeline.Item>
                    <Timeline.Item color="red"></Timeline.Item>
                    <Timeline.Item color="red"></Timeline.Item>
                    <Timeline.Item>
                        
                    </Timeline.Item>
                    <Timeline.Item
                        dot={
                            <Icon
                                type="clock-circle-o"
                                style={{ fontSize: "16px" }}
                            />
                        }
                    >
                        2015
                    </Timeline.Item>
                </Timeline>
            </section>
        );
    }
}
